import React,{useEffect,useRef,useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { useHistory } from "react-router-dom";
import EditIcon from '@material-ui/icons/Edit';

import Bar from "./graphs/bar"
import Funnel from "./graphs/funnel"
import Area from "./graphs/area"
import Line from "./graphs/line"
import Pie from "./graphs/pie"

const useStyles = makeStyles({
  root: {
    maxWidth: "100%",
    borderRadius:0,
    boxShadow:"none",
    // minHeight:300
  },
  media: {
    height: 140,
  },
});

export default function ProjectCard(props) {
  const classes = useStyles();
  const history = useHistory();
  const [width,SetWidth] = useState(400)

  const ref = useRef(null);
  useEffect(()=>{
    console.log('width', );
    SetWidth(ref.current ? ref.current.offsetWidth : 400)
  },[])

  const details=props.details
console.log(details)
  return (
    <Card className={classes.root} >
      {props.editable && props.editable==true && (
          <Button onClick={()=>props.setEdit({show:true,type:"edit",id:details._id})} className={classes.addCard}>
          <EditIcon />
      </Button>
       )}
      <span style={{padding:"0 10px"}}>
      {details.title}

      </span>
      <CardActionArea  >
       
        <CardContent>
          {details.type=='Bar' && (
            <div ref={ref}>
            <Bar width={width} api={details.api} render={details.render}/>
          </div>
          )}

          {details.type=='Area' && (
            <div ref={ref}>
            <Area width={width} api={details.api} render={details.render}/>
          </div>
          )}
          
          {details.type=='Line' && (
            <div ref={ref}>
            <Line width={width} api={details.api} render={details.render}/>
          </div>
          )}

        {details.type=='Pie' && (
            <div ref={ref}>
            <Pie width={width} api={details.api} render={details.render}/>
          </div>
          )}

{details.type=='Funnel' && (
            <div ref={ref}>
            <Funnel width={width} api={details.api} render={details.render}/>
          </div>
          )}
          {details.type=='Image' && (
            <div ref={ref}>
              <img width={width} height="200" src={details.api} />
          </div>
          )}
        </CardContent>
      </CardActionArea>
    </Card>
  );
}