import React, {useEffect,useState} from 'react';
import logo from './logo.svg';
import './App.css';
import Header from  './components/header'
import Footer from  './components/footer'
import Home from  './screens/home'
import Admin from  './screens/admin'
import Login from  './screens/login'
import Single from  './screens/singleProject'
import Setting from  './screens/setting'
import {Container} from '@material-ui/core'

import { makeStyles } from '@material-ui/core/styles';
import {BrowserRouter as Router,Switch,Route,Redirect} from 'react-router-dom'

import { useSelector, useDispatch } from 'react-redux'
import {loginUser,logoutUser,viewUser} from './reducers/userSlice'
import {loadSetting,viewSetting} from './reducers/settingSlice'
import axios from 'axios'
import 'fontsource-roboto';
import config from './config'

const theme = {
  background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
};


// const useStyles = makeStyles((theme) => ());

function ProtectedRoute({ component: Component, ...rest }){
  const user = useSelector(viewUser)

  return(
    <Route {...rest} render={props=> user.username ? (<Component {...props} />) : (<Redirect to={{pathname: "/login"}} /> ) } />
  )
 
}



function App() {
  const user = useSelector(viewUser)
  const settings = useSelector(viewSetting)
  const [checkComplete,setCheckComplte] = useState(false)
  const dispatch = useDispatch()
  const [classes,setClasses] = useState({
    body:{
      background:settings.body
    },
    container:{
      padding:"0px 0px"
   }
  })

  console.log("environment",config)

  useEffect(()=>{
    axios({
       method:'GET',
       withCredentials:true,
       url:'http://localhost:3001/user/me'
    }).then(res=>{
        console.log("checking_login", res)
      
        if(res){
            dispatch(loginUser(res.data))
        }
        setCheckComplte(true)
    }).catch((err)=>{
        setCheckComplte(true)
    })

    axios.get(config.REMOTE_SETTING_URL).then(res=>{
      dispatch(loadSetting(res.data[0]))
      const {bodyColor} = res.data[0]
      setClasses(old=>({...old,body:{background:bodyColor}}))
      document.body.style.background=bodyColor
    }).catch(err=>{
      console.log(err);
      axios.get(config.SETTING_URL).then(res=>{
      dispatch(loadSetting(res.data[0]))
      const {bodyColor} = res.data[0]
      setClasses(old=>({...old,body:{background:bodyColor}}))
      document.body.style.background=bodyColor
     })

    })
  },[])


  
 
  return (
    checkComplete && (
      
    <div id="body" style={classes.body}>
      <Router>
        <Header/>
          <Container style={classes.container}>
          <Switch>
            <ProtectedRoute path="/admin" component={Admin} />
            <Route path="/login" component={Login} />
            <Route path="/project/:id" component={Single} />
            <ProtectedRoute path="/setting" component={Setting} />
            <Route path='/' component={Home} />
            
          </Switch>
        
          </Container>
          <Footer />

      </Router>
              

    
      </div>
    )
    
  );
}

export default App;
