import React, {useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {TextField} from '@material-ui/core';
import Axios from 'axios';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export default function DialogSelect(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [fields, setFields] = React.useState({
      title:""
  });

  const handleChange = (event) => {
      const {id,value} = event.target;
      setFields(old=>({...old,[id]:value}))
  };

  const handleSubmit = ()=>{
    Axios.post('http://localhost:3001/category/add-new',fields).then(res=>{
        console.log(res.data)
        const {message} = res.data;
        if(message=="error"){
            console.log(res.data.errors)
        }
        if(message=="updated"){
            const {date,...rest} = res.data._doc
            
            console.log(res.data._doc)
        }
        if(message=="created"){
            const {date,...rest} = res.data._doc
            console.log(res.data._doc)
            handleClose()
        }
        
    
        })
  }

  useEffect(()=>{
    if(props.show==true){
        handleClickOpen()
    }else{
        handleClose()
    }
  },[props.show])
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    props.toggler(false)
  };

  return (
    <div>
      <Dialog disableBackdropClick disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle>Fill the form</DialogTitle>
        <DialogContent>
            <TextField value={fields.title} id="title" onChange={handleChange} label="Title"/>

        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSubmit} color="primary">
            Create
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
