import React, {useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {Link} from 'react-router-dom'
import { useHistory } from "react-router-dom";
import axios from 'axios'
import SettingsIcon from '@material-ui/icons/Settings';

import { useSelector, useDispatch } from 'react-redux'
import {viewUser,logoutUser,loginUser } from '../reducers/userSlice'
import {viewSetting} from '../reducers/settingSlice'
import Welcome from './welcome'

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    barColor:{
        background:"white"
    },
    link:{
        textDecoration:'none'
    }

}));
 
const Header = ()=>{
    const setting = useSelector(viewSetting)

    const classes = useStyles();
    const user = useSelector(viewUser)
    const dispatch = useDispatch()
    const history = useHistory();

   

    function handleLogout(){
        axios({
            method:'POST',
            withCredentials:true,
            url:'http://localhost:3001/user/logout'
        }).then(()=>{
            console.log('logout')
            dispatch(logoutUser());
            history.push('/')

        }).catch(err=>{
            console.log(err)
        })
    }

    return (

        <div style={{background:"url(https://i.pinimg.com/originals/2e/e6/99/2ee6998e34c3e2eff7b894c66cfc5267.jpg) no-repeat",backgroundSize:"cover",backgroundPositionY:"center"}}>
            <div style={{background:setting.headerColor+"2B"}}>
            <AppBar position="static" color={'transparent'} style={{boxShadow:"none"}}  >
            <Toolbar>
                <a href="/"><img style={{height:"50px",padding:"8px"}}src="/logo.png" /></a>
                <Typography variant="h6" style={{color:setting.menuLinkColor}}  className={classes.title}>
               {setting.siteName}
                </Typography>

                {/* <Link to='/' className={classes.link}  >
                    <Button style={{color:setting.menuLinkColor}}>Home</Button>
                </Link> */}
                { user.username ? (
                    <>
                    <Link to='/admin' className={classes.link}  >
                        <Button style={{color:setting.menuLinkColor}}>Admin</Button>
                    </Link>
                    <Link to='/setting' className={classes.link}  >
                        <Button style={{color:setting.menuLinkColor}}><SettingsIcon /></Button>
                    </Link>
                    <Button color="inherit" onClick={handleLogout}>Logout</Button>

                     
                    </>
                ) : (
                null
                )}

                
                
            </Toolbar>
        </AppBar>
        <Welcome />
            </div>
       
        </div>
    )
}


export default Header