
import React, {useEffect,useState}  from 'react';
import {PieChart,Cell,Tooltip,Legend,Pie} from 'recharts'
import Axios from 'axios'
import {Box} from '@material-ui/core'
import Table from '../table'

export default function (props){
    const [data,setData] = useState([])
    const [loaded,setLoaded] =useState(false)
    const [genData,setGenData] = useState({})

    const [header,setHeader] = useState([])
    const render = props.render;


    useEffect(()=>{
        Axios.get(props.api).then(res=>{
            console.log(res.data)
            setData(res.data.graphData)
            setGenData(res.data.genData)

            setHeader(["S.No",...res.data.genData.cols]);

            setLoaded(true)

        })
    },[])

    function getRandomColor() {
        var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
          color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
      }
    
    return(
        loaded==true && (
            <>
             {(render=='graph'||render=='both') && (
            <PieChart  width={props.width} height={200} >
           
            <Tooltip />
            <Legend />
          
                  <Pie data={data} cx="50%" cy="50%" outerRadius={80}  >
                  {
                        data.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={getRandomColor()}/>
                        ))
                        }
                    </Pie>
         
            </PieChart>
             )}

            {(render=='table'||render=='both') && (
            <div>
                <Box mt={3}>
                    <Table header={header} row={data} />

                </Box>
               
            </div>
            )}
            </>
        )
            
        
    )
}