import React, {useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory } from "react-router-dom";
import {BottomNavigation, Box} from '@material-ui/core'


import { useSelector, useDispatch } from 'react-redux'
import {viewUser,logoutUser,loginUser } from '../reducers/userSlice'
import {viewSetting} from '../reducers/settingSlice'


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    barColor:{
        background:"white"
    },
    link:{
        textDecoration:'none'
    }

}));
 
const Footer = ()=>{
    const setting = useSelector(viewSetting)

    const classes = useStyles();
    const user = useSelector(viewUser)
    const dispatch = useDispatch()
    const history = useHistory();

   
    return (
        <Box p={2} style={{position:"relative",bottom:"0px",textAlign:"center",backgroundColor:setting.footerColor}}>
            {setting.copyright}
        </Box>
    )
}


export default Footer