import React, {useEffect,useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ProjectCard from '../components/projectCard'
import {Container, Grid, Button,Card} from '@material-ui/core'
import axios from 'axios'
import AddIcon from '@material-ui/icons/Add';

import AddProjectForm from '../components/addProjectFrom';

const useStyles = makeStyles((theme) => ({
   

    addCard:{
        minHeight:300,
        minWidth:"100%",
        display:'flex',
        justifyContent:'center',
        alignItems:'center'
    }
}));

const Admin = ()=>{

    const classes = useStyles();
    const [pageNo,setPageNo] = useState(1);
    const [cards,setCards] = useState([]);

    const [form,setForm] = useState({show:false,type:"new",id:null})
    const [hasNextPage,setHasNextPage] = useState(false)
    const [hasPrevPage,setHasPrevPage] = useState(false)

    useEffect(()=>{
        getCards(1)
    },[])
    
    function getCards(page){
        axios.get('http://localhost:3001/project?no='+page).then((res)=>{ 
            console.log(res)
            if(res.data.docs){
                setCards(res.data.docs)
                setPageNo(res.data.page)
                setHasNextPage(res.data.hasNextPage)
                setHasPrevPage(res.data.hasPrevPage)
            }

        })
    }

    function handlePaging(type){
        console.log(type)
        if(type=='next'){
            getCards(pageNo+1)
        }
        if(type=='prev'){
            getCards(pageNo-1)
            
        }

        
    }
    return (
        form.show ? (
            <AddProjectForm edit={form} toggler={setForm}/>
        ):(
            <div>  
            <Grid container spacing={1}>
                   
                {cards.length > 0 ? (
                    <>
                    {cards.map((card,index)=>{
                        return (
                            <Grid key={"prod_"+index} item  xs={12} sm={4} md={card.col}>
                               
                                <ProjectCard editable={true} setEdit={setForm} details={card}/>
                            </Grid>
                        )
                    })}

                    <Grid item  xs={12} sm={4} md={3} xl={2}>
                    <Button onClick={()=>setForm({show:true,type:"new",id:null})} className={classes.addCard}>
                        <AddIcon/>
                    </Button>
                </Grid>
                    </>
                ):(
                    <Grid item  xs={12} sm={6} md={3} xl={2}>
                    
                    </Grid>
                )}
            </Grid>
            <Grid container >
                <Grid item>
                
                    {hasPrevPage && (
                        <Button variant="contained" onClick={()=>handlePaging('prev')} color="primary">
                            Previous
                        </Button>
                    )}
                    {hasNextPage && (
                        <Button variant="contained" onClick={()=>handlePaging('next')} color="primary">
                            Next
                        </Button>
                    )}
                    
                </Grid>
            </Grid>
        
        </div>
        )
        

    )
}

export default Admin