import { Box, Button, Grid, Input, Paper, TextField, Typography } from '@material-ui/core'
import { ColorPicker } from 'material-ui-color';
import { useSelector, useDispatch } from 'react-redux'
import {loadSetting,viewSetting} from '../reducers/settingSlice'
import Exporter from '../components/util/exporter'
import React from 'react'
import Axios from 'axios'

import { PersonalVideoTwoTone } from '@material-ui/icons';

const colorschema = {
    style1:["#07689f","#ffffff","#07689f2B"],
    style2:["#112d32","#ff2e63","#112d322B"],
    style3:["#1fab89","#d7fbe8","#1fab892B"],
    style4:["#155263","#ffc93c","#ff6f3c2B"],
    style5:["#008AA5","#000","#99e1e52B"],
}
export default function Setting(){
    const setting = useSelector(viewSetting)
    const dispatch = useDispatch()
    const [fields,setFields] = React.useState({
        siteName:"",
        bodyColor:"",
        headerColor:"",
        menuLinkColor:"",
        copyright:"",
        infoTextMain:"",
        infoTextSub:"",
    })


    React.useEffect(()=>{
        Axios.get('http://localhost:3001/setting/').then(res=>{
            dispatch(loadSetting(res.data[0]))
            setFields(res.data[0])

          })
    },[])

    function handleChange(e){
        const {id,value} = e.target
        setFields(old=>({...old,[id]:value}))
    }


   
      
    function save(){
        Axios.post('http://localhost:3001/setting/update',fields).then(res=>{
        console.log(res.data)
        window.location.reload('/')

        const {message} = res.data;
        if(message=="error"){
            console.log(res.data.errors)
            alert.show(res.data.errors.map(each=>each.msg).join(", "))
        }
        if(message=="updated"){
            
            console.log(res.data._doc)
            alert.show("Updated")
        }
        if(message=="created"){
            console.log(res.data._doc)
            alert.show("Created")
        }
        
        })
    }

    function setColors(id){
        console.log(id)
        const colors = colorschema[id]
        setFields(old=>({...old,bodyColor:colors[2],headerColor:colors[0],menuLinkColor:colors[1]}))
    }

    function getFiles(){
        Axios.get('http://localhost:3001/setting/').then(res=>{
            downloadObjectAsJson(res.data, 'setting')
          })
          Axios.get('http://localhost:3001/project?no=1').then(res=>{
            downloadObjectAsJson(res.data, 'all')
          })
    }

    function downloadObjectAsJson(exportObj, exportName){
        var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(exportObj));
        var downloadAnchorNode = document.createElement('a');
        downloadAnchorNode.setAttribute("href",     dataStr);
        downloadAnchorNode.setAttribute("download", exportName + ".json");
        document.body.appendChild(downloadAnchorNode); // required for firefox
        downloadAnchorNode.click();
        downloadAnchorNode.remove();
      }
    return (
        <Box>
            <Grid container  justify='center' alignContent="center" alignItems='center' spacing={3}>

            <Grid item xs={12}>
            <Box style={{background:"white"}} p={3} m={2}>
               <Grid container direction="row">
               {
                  Object.entries(colorschema).map(([key,value])=>{
                       return (
                           <Box m={2} id={key} onClick={()=>setColors(key)}>
                            {value.map(col=>{
                            return <div style={{width:"200px",height:"50px",background:col}}></div>
                            })}
                            </Box>
                       ) 
                  })                 
               }
               </Grid>
               
                <Typography variant="h6" gutterBottom>Header</Typography>

                <Grid item >
                <TextField label="Site Name" type="text" value={fields.siteName} id="siteName" onChange={handleChange} ></TextField>   
                </Grid>
                            <Grid container>
                            <Grid item xs={12} sm={4} >
                <p>header color</p>
                    <ColorPicker
                    name='color'
                    label="Header Color"
                    id="headerColor"
                    value={fields.headerColor}
                    onChange={color=>setFields(old=>({...old,headerColor:color.css.backgroundColor}))}
                    />
                </Grid>
                <Grid item xs={12} sm={4} >
                <p>Background color</p>
                    <ColorPicker
                    name='color'
                    label="Body Color"
                    id="bodyColor"
                    value={fields.bodyColor}
                    onChange={color=>setFields(old=>({...old,bodyColor:color.css.backgroundColor}))}
                    />
                </Grid>
                <Grid item xs={12} sm={4} >
                <p>Menu Link color</p>
                    <ColorPicker
                    name='color'
                    label="Menu Link Color"
                    id="menuLinkCOlor"
                    value={fields.menuLinkColor}
                    onChange={color=>setFields(old=>({...old,menuLinkColor:color.css.backgroundColor}))}
                    />
                </Grid>  
                            </Grid>
               
                    <Button onClick={save}>Save</Button>
                </Box>

                <Box style={{background:"white"}} p={3} m={2}>
                <Typography variant="h6" gutterBottom>Info Section</Typography>
        
                <TextField  label="Main Text" rows={2} fullWidth type="text" value={fields.infoTextMain} id="infoTextMain" onChange={handleChange}></TextField>

                <TextField  label="Copyright" multiline rows={2} fullWidth type="text" value={fields.infoTextSub} id="infoTextSub" onChange={handleChange}></TextField>

                    <p>Background color</p>
                    <ColorPicker
                    name='color'
                    label="Info Background Color"
                    id="infoBgColor"
                    value={fields.infoBgColor}
                    onChange={color=>setFields(old=>({...old,infoBgColor:color.css.backgroundColor}))}
                    />

                    <Button onClick={save}>Save</Button>
                </Box>

                <Box style={{background:"white"}} p={3} m={2}>
                <Typography variant="h6" gutterBottom>Footer</Typography>

                <TextField  label="Copyright" multiline rows={2} fullWidth type="text" value={fields.copyright} id="copyright" onChange={handleChange}></TextField>
                    
                    <p>Footer color</p>
                    <ColorPicker
                    name='color'
                    label="footer Color"
                    id="footerColor"
                    value={fields.footerColor}
                    onChange={color=>setFields(old=>({...old,footerColor:color.css.backgroundColor}))}
                    />

                    

                    <Button onClick={save}>Save</Button>

                    <Button color='primary' onClick={getFiles}>Export</Button>
                </Box>
                
                
            </Grid>
              
            </Grid>
        </Box>
    )
}