import React,{useState,useEffect} from 'react'
import {Grid, Button,TextField, Card,FormControl,InputLabel,Input,Switch, Select, MenuItem, InputBase} from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import Axios from 'axios';
import NewCategoryPopup from './newCategoryPopup'
import { useAlert } from "react-alert";


const useStyles = makeStyles((theme) => ({ 
    grid:{
      padding:"10px 10px"
    },
    fields:{
        minWidth:"100%",
        padding:"5px 4px",
        margin:"10px 0"
    }
}));

function dateFormat(date){
    var date = new Date(date);  // dateStr you get from mongodb

    var d = date.getDate();
    var m = date.getMonth()+1;
    var y = date.getFullYear();
    return y+"-"+m+"-"+d
}

function ArrayInput(prop){

    const classes = useStyles()
    const [items,setItems] = useState([]);
    const [item,setItem] = useState("");
    const [loaded,setLoaded] = useState(false)

    useEffect(()=>{

        setItems(prop.values)
        setLoaded(true)
    },[])

    function handleChange(e){
        const {value} = e.target
        
            setItem(value)
        
        
    }
    function setValues(){
        setItems((old)=>{

            if(item.length > 0){
                prop.setter({[prop.fieldName]:[...old,item]})
                return [...old,item]
            }

            return old
           
        })
       
        setItem("")
    }
    function removeItem(index){

        console.log(index)
        let newList = items
        newList.splice(index,1)
        setItems((old)=>{

            prop.setter({[prop.fieldName]:newList})
            return newList
        })
        
    }
    return(
        loaded==true &&(
        <div >
        {items.map((item,index)=>{
            return (<p key={prop.fieldName+index}><Button onClick={(e)=>removeItem(index)}> x </Button>{item}</p>)
        })}
        <FormControl fullWidth >
        <Input onChange={handleChange} placeholder={"Add Multiple "+prop.fieldName}  multiline={prop.multiline} rows={prop.rows} value={item} endAdornment={
            (<Button onClick={setValues}>Add</Button>)
        }/>
        </FormControl>
      
        
        </div>
        )
       
    )

}

export default function ProjectForm(props){
    const classes = useStyles()
    const [loaded,setLoaded] = useState(false)
    const [showNewCategForm,setshowNewCategForm] = useState(false)
    const [previews,setPreviews] = useState([]);
    const alert = useAlert();

    const [fields,setFields]=useState({
        title:"",
        description:"",
        type:"",
        api:"",
        render:"",
        cols:3,
    });

    const [categories,setCategories] = useState([])

    useEffect(()=>{
        console.log(fields)
        loadCategories()
    },[showNewCategForm])

    useEffect(()=>{

        loadCategories()

        if(props.edit.type=="edit"){
            Axios.get('http://localhost:3001/project/single/'+props.edit.id)
            .then((res)=>{
               if(res){
                   console.log(res.data)
                const data = res.data[0]
                setPreviews([])
               
                setFields(data)
                setLoaded(true)
               }
              
                
            })
        }else{
            setLoaded(true)

        }
    
    },[])

    function loadCategories(){
        Axios.get('http://localhost:3001/category/')
        .then((res)=>{
           if(res){
               console.log(res.data)
                setCategories(res.data)
           }     
        })
    }
    function textFieldHandle(e){
        const {value,id,name} = e.target;
        console.log(e.target)
        setFields((prev)=>({...prev,[id || name]:value}))
    }
  
    function handleSwitch(e){
        const {checked,id} = e.target;
        setFields((prev)=>({...prev,[id]:checked}))

    }
    function handleFiles(e){
        const files = e.target.files;
        setFields((prev)=>({...prev,files:files}))
        setPreviews([]);
        const length = files.length
        if (length >0  ) {
            for(let i=0;i<length;i++){
                var reader = new FileReader();
            
                reader.onload = function(e) {
                  console.log(e.target.result)
                  setPreviews(old=>([...old,e.target.result]))
                }
                
                reader.readAsDataURL(files[i]); 
            }
           // convert to base64 string
          }

    }

    function setFieldsArray(object){
        console.log("in Array Setter",object)
        setFields((prev)=>({...prev,...object}))
    }

    function submitForm(){
        const data = new FormData()
        Object.entries(fields).map(each=>{
            if(each[0]!='files'){
                data.append(each[0],each[1])
            }else{
                for(var x = 0; x<each[1].length; x++) {
                    data.append('file', each[1][x])
                }
            }
        })
        
        console.log(data)

        Axios.post('http://localhost:3001/project/add-new',data).then(res=>{
        console.log(res.data)
        const {message} = res.data;
        if(message=="error"){
            console.log(res.data.errors)
            alert.show(res.data.errors.map(each=>each.msg).join(", "))
        }
        if(message=="updated"){
            const {date,...rest} = res.data._doc
            
            console.log(res.data._doc)
            setFields({...rest,date:dateFormat(date)})
            alert.show("Updated")
            props.toggler({show:false,type:"new",id:null})
        }
        if(message=="created"){
            const {date,...rest} = res.data._doc
            console.log(res.data._doc)
            setFields({...rest,date:dateFormat(date)})
            alert.show("Created")
            props.toggler({show:false,type:"new",id:null})
        }
        
    
        })
    }
    return(
        loaded==true ? (
        <>
        <Button onClick={()=>props.toggler({show:false,type:"new",id:null})}>Back</Button>
            <Grid container spacing={3} justify="center" className={classes.grid}>
                        <Grid item xs={12} sm={8} >
                            <Card className={classes.grid}>
                                <div>
                                <TextField className={classes.fields} fullWidth label="Title" onChange={textFieldHandle} id="title" value={fields.title} />
                                <TextField className={classes.fields} fullWidth label="Description"
                                    multiline
                                    rows={4}
                                    onChange={textFieldHandle} id="description" value={fields.description} />
                                </div>
                                <TextField className={classes.fields} fullWidth label="Type" onChange={textFieldHandle} name="type" value={fields.type} select>
                                    <MenuItem name="type" value="Bar" >Bar</MenuItem>
                                    <MenuItem name="type" value="Area" >Area</MenuItem>
                                    <MenuItem name="type" value="Line" >Line</MenuItem>
                                    <MenuItem name="type" value="Pie" >Pie</MenuItem>
                                    <MenuItem name="type" value="Funnel" >Funnel</MenuItem>
                                    <MenuItem name="type" value="Image" >Image</MenuItem>
                                    </TextField>
                                <TextField className={classes.fields} fullWidth label={fields.type=='Image'?"src":"API"} onChange={textFieldHandle} id="api" value={fields.api} />
                                <TextField className={classes.fields} fullWidth label="Render" onChange={textFieldHandle} name="render" value={fields.render} select>
                                    <MenuItem name="render" value="graph" >Graph</MenuItem>
                                    <MenuItem name="render" value="table" >Table</MenuItem>
                                    <MenuItem name="render" value="both" >Both</MenuItem>
                                    </TextField>
                                <TextField className={classes.fields} fullWidth label="Render" onChange={textFieldHandle} name="col" value={fields.col} select>
                                    {[3,6,9,12].map(each=>{
                                        return (<MenuItem name="render" value={each} >{each}</MenuItem>)
                                    })}
                                </TextField>
                                    
                                <Button onClick={submitForm}>Submit</Button>
                            </Card>
                        {/* title
                            description
                            date
                            [devs]
                            [problems]
                            [solutions]
                            [benefits]
                            signOff

                            */}
                        </Grid>
                        
                </Grid>
                <NewCategoryPopup show={showNewCategForm} toggler={setshowNewCategForm} >Open select dialog</NewCategoryPopup>
        </>
        ):(<p>Loading...</p>)
    )
   
}