
import React, {useEffect,useState}  from 'react';
import {LineChart,CartesianGrid,XAxis,YAxis,Tooltip,Legend,Line,ResponsiveContainer} from 'recharts'
import Axios from 'axios'
import {Box} from '@material-ui/core'
import Table from '../table'


export default function (props){
    const [data,setData] = useState([])
    const [loaded,setLoaded] =useState(false)
    const [genData,setGenData] = useState({})

    const [header,setHeader] = useState([])
    const render = props.render;

    useEffect(()=>{
        Axios.get(props.api).then(res=>{
            console.log(res.data)
            setData(res.data.graphData)
            setGenData(res.data.genData)

            setHeader(["S.No",res.data.genData.xAxisKey,...res.data.genData.barsKeys.map(each=>each[res.data.genData.xAxisKey])]);

            setLoaded(true)

        })
    },[])

    
    return(
        loaded==true && (
            <>
             {(render=='graph'||render=='both') && (
            <LineChart layout={genData.layout}  width={props.width} height={200} data={data}>
            <XAxis dataKey={genData.xAxisKey} />
            <YAxis width={40} />
            <Tooltip />
            <Legend />
            {genData && genData.barsKeys.map(each=>{
                return  <Line dataKey={each.name} type="monotone" stroke={each.color}/>
            })}
            </LineChart>
             )}
             {(render=='table'||render=='both') && (
            <div>
                <Box mt={3}>
                    <Table header={header} row={data} />

                </Box>
               
            </div>
            )}
            </>
        )
            
        
    )
}