import React,{useEffect,useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useParams } from "react-router-dom";
import LoginForm from  '../components/loginForm'
import {Grid,Card,Paper} from '@material-ui/core'
import Axios from 'axios'
import { Typography } from '@material-ui/core';
import Single from '../components/singleProject'
const useStyles = makeStyles((theme) => ({
    root: {
        minHeight:"70vh"
    },
    paper:{
        padding:"20px"
    }
    
}));



const SingleProject = ()=>{
    const classes = useStyles()
    const [loaded,setLoaded] = useState({load:false,data:false})
    const [details,setDetails] = useState({})
    const { id } = useParams();

    useEffect(()=>{
        Axios.get('http://localhost:3001/project/single/'+id)
        .then((res)=>{
           if(res){
               console.log(res.data)
               setDetails(res.data[0])
               setLoaded({load:true,data:true})
           }     
        }).catch(()=>{
            setLoaded({load:true,data:false})
        })

    },[])




    return(
        <Grid container  justify='center'  className={classes.root}>
            <Grid item xs={12} sm={9} md={8} >
                    {loaded.load==true && (
                        loaded.data==true ? (
                           <Single details={details}/>
                        ):(
                            <Typography variant="h5">
                                      Not Found          
                            </Typography>
                        )
                       
                    )}
                   


            </Grid>
        </Grid>

    )
}
export default SingleProject
