
import React, {useEffect,useState}  from 'react';
import {FunnelChart,Funnel,LabelList,Tooltip,Legend,Line,ResponsiveContainer} from 'recharts'
import Axios from 'axios'
import {Box} from '@material-ui/core'
import Table from '../table'


export default function (props){
    const [data,setData] = useState([])
    const [loaded,setLoaded] =useState(false)
    const [genData,setGenData] = useState({})

    const [header,setHeader] = useState([])
    const render = props.render;

    useEffect(()=>{
        Axios.get(props.api).then(res=>{
            console.log(res.data)
            setData(res.data.graphData)
            setGenData(res.data.genData)

            setHeader(["S.No",...Object.keys(res.data.graphData[0])]);

            setLoaded(true)

        })
    },[])

    
    return(
        loaded==true && (
            <>
             {(render=='graph'||render=='both') && (
                 <FunnelChart width={props.width} height={200}>
                 <Tooltip />
                 <Funnel
                   dataKey={genData.funnelKey}
                   data={data}
                   isAnimationActive
                 >
                   <LabelList position="right" fill="#000" stroke="none" dataKey={genData.nameKey} />
                 </Funnel>
               </FunnelChart>
             )}
             {(render=='table'||render=='both') && (
            <div>
                <Box mt={3}>
                    <Table header={header} row={data} />
                </Box>
               
            </div>
            )}
            </>
        )
            
        
    )
}