import React, {useState} from 'react';
import {TextField,Button,Input,InputLabel,FormHelperText} from '@material-ui/core';
import { useHistory } from "react-router-dom";
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios'
import { useAlert } from "react-alert";

import { useDispatch } from 'react-redux'
import {loginUser} from '../reducers/userSlice'

const useStyles = makeStyles((theme) => ({
    root: {
        Width:"100%",
        textAlign:'center'
    },
    fields:{
        minWidth:"100%",
        padding:"10px 4px",
        margin:"10px 0"
    }
    

}));



export default ()=>{
    const classes = useStyles();
    const history = useHistory();
    const dispatch = useDispatch();
    const alert = useAlert();



    const [details, setDetails] = useState({username:"",password:"",showPassword:false});
    const [errors, setErrors] = useState({username:"",password:""});

    function handleChange(e){
        const {value , id} = e.target
        console.log(value,id)
        setDetails((prev)=>({...prev,[id]:value}))
        setErrors({username:"",password:""})
    }
    
    const handleClickShowPassword = () => {
        setDetails({ ...details, showPassword: !details.showPassword });
      };

      const handleMouseDownPassword = (event) => {
        event.preventDefault();
      };

    const handleLogin = (event) =>{

        const data = {
            username:"asdasd",
            password:"password"
        }
        axios({
          method:'POST',
          data:details,
          withCredentials:true,
          url:"http://localhost:3001/user/login/"
        }).then(res=>{
            console.log(res.data)
            if(res.data.errors){
                setErrors(()=>{
                   let errs = res.data.errors.reduce((agg,each)=>{
                        return {...agg,[each.param]:each.msg}},{})
                    console.log(errs)
                    return errs
                })
                return;
            }
            if(res.data.message){
                setErrors((errs)=> ({...errs,['password']:res.data.message}))
                return;
            }

            
            dispatch(loginUser(res.data))    
            history.push('/admin')

          }).catch((error) => console.log( error) );


    }

    return (
        <form className={classes.root} method="post" noValidate autoComplete="off">
        <div  >
          <Input
          onChange={handleChange}
            className={classes.fields}
            error={errors.username ? true : false}
            id="username"
            placeholder="Username"
            value={details.username}
          />
                  <FormHelperText id="component-helper-text">{errors.username}</FormHelperText>

        </div>
        <div>
          <Input
             onChange={handleChange}

            className={classes.fields}
            error={errors.password ? true : false}
            type={details.showPassword ? 'text' : 'password'}
            id="password"
            value={details.password}
            placeholder="Password"
            helperText={errors.password}
            endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    onMouseDown={handleMouseDownPassword}
                  >
                    {details.showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
          />
                            <FormHelperText id="component-helper-text">{errors.password}</FormHelperText>

          
        </div>
        <div className={classes.fields}>
            <Button variant="contained" color='primary' onClick={handleLogin}>Login</Button>
           
        </div>
        
      </form>
    );
}