import React,{useRef,useState,useEffect} from 'react';
import { Typography, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
 
import Bar from "./graphs/bar"
import Line from "./graphs/line"
import Pie from "./graphs/pie"

const useStyles = makeStyles(()=>({

    title:{
        textAlign:'center',
        fontWeight:'700'
    },
    cardTitle:{
        fontSize:"15px",
        fontWeight:'700',
        textAlign:'center',
        padding:"8px"
    },
    padding:{
        padding:"10px"
    },
    bullet: {
        margin: '0 5px',
        transform: 'scale(2)',
    },
    bulletin:{
        overflowWrap: "break-word",
    }
}))
export default function(props){
    const classes = useStyles()
    const bull = <span className={classes.bullet}></span>;
    const [width,SetWidth] = useState(400)

    const ref = useRef(null);
    useEffect(()=>{
      console.log('width', );
      SetWidth(ref.current ? ref.current.offsetWidth : 400)
    },[])

    const details=props.details
    return (
        <>
        <Box my={4} className={classes.padding}>
            <Typography variant="h5" className={classes.title} gutterBottom>
                {details.title}
            </Typography>
            <Typography className={classes.title} variant="body2" color="textSecondary" component="p" gutterBottom>
                 {details.category ? details.category.title : 'UnCategorised'}
            </Typography>
            <Box p={2}>
                <Typography  variant="p" gutterBottom>
                    {details.description && details.description.split('\n').map((value, index) => {
                        return (
                        <span key={index}>
                            {value}
                            <br />
                        </span>
                        );
                    })}
                </Typography>
            </Box>
            
        </Box>
       
      
        <Box my={4}  p={2} bgcolor="white" borderRadius="10px">
        {details.type=='Bar' && (
            <div ref={ref}>
            <Bar width={width} api={details.api} render={details.render}/>
          </div>
          )}
          
          {details.type=='Line' && (
            <div ref={ref}>
            <Line width={width} api={details.api} render={details.render} />
          </div>
          )}

        {details.type=='Pie' && (
            <div ref={ref}>
            <Pie width={width} api={details.api} render={details.render}/>
          </div>
          )}
       
       
        </Box>
        </>
        
        
    )
}