
import React, {useEffect,useState}  from 'react';
import {AreaChart,CartesianGrid,XAxis,YAxis,Tooltip,Legend,Area,ResponsiveContainer} from 'recharts'
import Axios from 'axios'
import Table from '../table'
import {Box} from '@material-ui/core'
import TableChartIcon from '@material-ui/icons/TableChart';

export default function (props){
    const [data,setData] = useState([])
    const [loaded,setLoaded] =useState(false)
    const [genData,setGenData] = useState({})

    const [header,setHeader] = useState([])
    const render = props.render;

    useEffect(()=>{
        Axios.get(props.api).then(res=>{
            console.log(res.data)
            setData(res.data.graphData)
            setGenData(res.data.genData)
            setLoaded(true)

            setHeader(["S.No",res.data.genData.xAxisKey,...res.data.genData.barsKeys.map(each=>each[res.data.genData.xAxisKey])]);

        })
    },[])

    console.log("render",render)
    return(
        loaded==true && (
            <>
            {(render=='graph'||render=='both') && (
                 <AreaChart layout={genData.layout}  width={props.width} height={200} data={data}>
                <defs>
                    {genData && genData.barsKeys.map(each=>{
                    return <linearGradient id={"color_"+each.name} x1="0" y1="0" x2="0" y2="1">
                        <stop offset="5%" stopColor={each.color} stopOpacity={0.8}/>
                        <stop offset="95%" stopColor={each.color} stopOpacity={0}/>
                    </linearGradient>
                })}
                </defs>
               
                <XAxis dataKey={genData && genData.xAxisKey} />
                <YAxis width={40} />
                <Tooltip />
                <Legend />
                {genData && genData.barsKeys.map(each=>{
                    return <Area type="monotone" dataKey={each.name} stroke={each.color} fillOpacity={1} fill={"url(#color_"+each.name+")"} />
                })}
                </AreaChart>
            )}
            
            {(render=='table'||render=='both') && (
                
            <div>
                <Box mt={3}>
                    <Table header={header} row={data} />

                </Box>
               
            </div>
            )}
            </>
        )
            
        
    )
}