
import React, {useEffect,useState}  from 'react';
import {BarChart,CartesianGrid,XAxis,YAxis,Tooltip,Legend,Bar,ResponsiveContainer} from 'recharts'
import Axios from 'axios'
import Table from '../table'
import {Box} from '@material-ui/core'
import TableChartIcon from '@material-ui/icons/TableChart';

export default function (props){
    const [data,setData] = useState([])
    const [loaded,setLoaded] =useState(false)
    const [genData,setGenData] = useState({})

    const [header,setHeader] = useState([])
    const render = props.render;

    useEffect(()=>{
        Axios.get(props.api).then(res=>{
            console.log(res.data)
            setData(res.data.graphData)
            setGenData(res.data.genData)
            setLoaded(true)

            setHeader(["S.No",res.data.genData.xAxisKey,...res.data.genData.barsKeys.map(each=>each[res.data.genData.xAxisKey])]);

        })
    },[])

    console.log("render",render)
    return(
        loaded==true && (
            <>
            {(render=='graph'||render=='both') && (
                <BarChart layout={genData.layout}  width={props.width} height={200} data={data}>
                <XAxis dataKey={genData && genData.xAxisKey} />
                <YAxis width={40} />
                <Tooltip />
                <Legend />
                {genData && genData.barsKeys.map(each=>{
                    return  <Bar dataKey={each.name} fill={each.color}/>
                })}
                </BarChart>
            )}
            
            {(render=='table'||render=='both') && (
                
            <div>
                <Box mt={3}>
                    <Table header={header} row={data} />

                </Box>
               
            </div>
            )}
            </>
        )
            
        
    )
}