import React, {useEffect,useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ProjectCard from '../components/projectCard'
import {Container, Grid, Button,Typography} from '@material-ui/core'
import About from '../components/about'
import Welcome from '../components/welcome'
import axios from 'axios'
import config from '../config'
const useStyles = makeStyles((theme) => ({
   catTitle:{
       color:"red",
       alignItems:'center',
       padding:"0px 10px"
   }
    
}));

const Home = ()=>{

    const classes = useStyles();
    const [pageNo,setPageNo] = useState(1);
    const [cards,setCards] = useState([]);

    const [hasNextPage,setHasNextPage] = useState(false)
    const [hasPrevPage,setHasPrevPage] = useState(false)

    useEffect(()=>{
        getCards(1)
    },[])
    
    function getCards(page){
        axios.get(config.REMOTE_MAIN_URL+page).then((res)=>{ 
            console.log(res)
            if(res.data.docs){
                setCards(res.data.docs)
                setPageNo(res.data.page)
                setHasNextPage(res.data.hasNextPage)
                setHasPrevPage(res.data.hasPrevPage)
            }

        }).catch(err=>{
            console.log(err)
            axios.get(config.MAIN_URL).then((res)=>{ 
            console.log(res)
            if(res.data.docs){
                setCards(res.data.docs)
                setPageNo(res.data.page)
                setHasNextPage(res.data.hasNextPage)
                setHasPrevPage(res.data.hasPrevPage)
            }

            })
        })
    }

    function handlePaging(type){
        console.log(type)
        if(type=='next'){
            getCards(pageNo+1)
        }
        if(type=='prev'){
            getCards(pageNo-1)
            
        }

        
    }
    let categRender ='';
    return (
        <>
        <div style={{padding:"10px 10px"}}>       
            <Grid container spacing={0}>
           
          
               {cards.length > 0 ? (
                    cards.map((card)=>{
                        
                        return (
                            <>
                            <Grid item  xs={12} sm={4} md={card.col}>
                                <ProjectCard details={card}/>
                            </Grid>
                            </>
                        )
                    })
                    
                ):(
                    <Grid item  xs={12} sm={6} md={3} xl={2}>
                    
                    </Grid>
                )}
                        
                    
            </Grid>
            <Grid container >
                <Grid item>
                
                    {hasPrevPage && (
                        <Button variant="contained" onClick={()=>handlePaging('prev')} color="primary">
                            Previous
                        </Button>
                    )}
                    {hasNextPage && (
                        <Button variant="contained" onClick={()=>handlePaging('next')} color="primary">
                            Next
                        </Button>
                    )}
                    
                </Grid>
            </Grid>
            
        </div>
        <About />
        </>

    )
}

export default Home