import React, {useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import {Box,Grid} from '@material-ui/core'
import {Zoom,Slide} from '@material-ui/core'

import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import {Link} from 'react-router-dom'
import { useHistory } from "react-router-dom";
import axios from 'axios'
import SettingsIcon from '@material-ui/icons/Settings';

import { useSelector, useDispatch } from 'react-redux'
import {viewUser,logoutUser,loginUser } from '../reducers/userSlice'
import {viewSetting} from '../reducers/settingSlice'


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    barColor:{
        background:"white"
    },
    link:{
        textDecoration:'none'
    },
    leftLogo:{
        fontSize:"9rem",
        textAlign:"center"
    },
    ln:{
        fontSize:"1rem",
        fontWeight:"500",
        lineHeight:"2rem"
    },
    lns:{
        fontSize:"0.8rem",
        fontWeight:"400",
        lineHeight:"2rem"
    }
    

}));
 
const Header = ()=>{
    const setting = useSelector(viewSetting)
    const [end,setEnd] = React.useState(false)
    const classes = useStyles();


    window.onscroll = function(ev) {
        if ((window.innerHeight + window.scrollY) >= 1000) {
            setEnd(true)
        }else{
            setEnd(false)
        }
    };

    return (
        <Box py={14} style={{background:setting.infoBgColor}}>
        <Grid container justify="center" spacing={3}>

            <Grid item xs={16} sm={12} md={2}>
                 <Zoom in={end} style={{ transformOrigin: '0 0 0' }}
          {...(end ? { timeout: 1000 } : {})} >
                    <Typography variant="h3" className={classes.leftLogo} gutterBottom>
                        Gr
                    </Typography>
                </Zoom>
            </Grid>
            
            <Grid item xs={12} sm={12} md={3} >
                <div style={{padding: "0px 0px 0px 2rem",
        borderLeft: "0.5rem solid rgb(81, 179, 240)",textAlign:"left"}} >
                <Slide direction="left" in={end} style={{ transformOrigin: '0 0 0' }}
          {...(encodeURI ? { timeout: 1000 } : {})}>
                    <div>
                        
                        <Typography variant="h6" className={classes.ln} gutterBottom>
                    {setting.infoTextMain}
                    </Typography>
                    <Typography variant="p" className={classes.lns} gutterBottom>
                    {setting.infoTextSub}
                    </Typography>
                    </div>
                </Slide>
                
                </div>
                

                
                
            </Grid>
        </Grid>
        </Box>
    )
}


export default Header