import React, {useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Box,Grid,Typography} from '@material-ui/core'
import { useHistory } from "react-router-dom";
import axios from 'axios'
import SettingsIcon from '@material-ui/icons/Settings';
import Zoom from '@material-ui/core/Zoom';
import { useSelector, useDispatch } from 'react-redux'
import {viewUser,logoutUser,loginUser } from '../reducers/userSlice'
import {viewSetting} from '../reducers/settingSlice'


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    main:{
        color:"white",
        fontSize:"3.5rem",
        textAlign:"center",
        fontWeight:"500",
        
    },
    sub:{
        color:"white",
        fontSize:"2.5rem",
        textAlign:"center",
        fontWeight:"500"
    }

}));
 
const Header = ()=>{
    const setting = useSelector(viewSetting)

    const classes = useStyles();
    const user = useSelector(viewUser)
    const dispatch = useDispatch()
    const history = useHistory();

   

    function handleLogout(){
        axios({
            method:'POST',
            withCredentials:true,
            url:'http://localhost:3001/user/logout'
        }).then(()=>{
            console.log('logout')
            dispatch(logoutUser());
            history.push('/')

        }).catch(err=>{
            console.log(err)
        })
    }

    return (
        <Box py={8}>
        <Grid container justify="center" >
            
            <Grid item xs={12} sm={12} md={8} >
                <Zoom in={true} style={{ transitionDelay: '500ms'}} >
                    <Typography variant="h4" className={classes.main} gutterBottom>
                    Welcome to DAAShboard
                    </Typography>
                </Zoom>
                <Zoom in={true} style={{ transitionDelay: '800ms'}}>
                    <Typography variant="h5" className={classes.sub} gutterBottom>
                    your one stop solution for all web development
                    </Typography>
                </Zoom>

                
                
            </Grid>
        </Grid>

    </Box>
    )
}


export default Header